<template>
    <div class="site-wrapper">
        <div class="zakaz-page">
            <div class="container-fluid">
                <div class="row">
                    <div class="section-1">
                        <div class="info-a-eat"><router-link @click.native="clear()"  to="chacklist" class="close-pay">Закрыт счет</router-link>
                            <div class="my-spans"><span>Общее:</span>  <span class="my-2-span">{{sum}} uzs</span>
                            </div>
                            <a  @click="orderpost();loading=true">
                                <b-spinner v-if="loading" label="Loading..." variant="white"></b-spinner>
                              <spam v-if="!loading">Заказать</spam>
                            </a>
                        </div>
                    </div>
                    <div class="section-2 section-one-2">
                        <div class="row">
                            <div class="col-12 mar-bot" v-for="(item,index) in foodslist" :key="index">
                                <div class="eat-card-2">
                                    <div class="first">
                                        <div class="eat-img">
                                            <img :src="item.food.poster" alt=""  class="img_child">
                                        </div>
                                        <div class="dop-info">
                                            <div class="eat-name">{{item.food.title}}</div>
                                            <div class="eat-sub">{{item.food.description}}</div>
                                            <div class="eat-info"><span>С/п: {{item.food.price}} uzs </span><span><img src="../assets/images/icons/clock.svg" alt=""> 15м</span>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="second">
                                        <div class="how-much" :class="'countFood'+item.id">
                                            <div @click="minus(foodslist,index,count)" class="he-minus">
                                                <img src="../assets/images/icons/minus-black.svg" alt="">
                                            </div>
                                            <input  v-model="foodslist[index].count"   disabled="disabled" class="he-number">
                                            <div @click="pilus(foodslist,index,count)" class="he-plus">
                                                <img src="../assets/images/icons/plus-black.svg" alt="">
                                            </div>
                                        </div><span>{{item.food.price}} uzs</span>
                                    </div>
                                    <div class="minus-3"  @click="deletee=('qwertymy'+index)" >
                                        <img src="../assets/images/icons/minus-3.svg" alt="">
                                    </div>
                                    <button @click="foodslist.splice(index,1);sum=sum-food.price"   class="third"  :class="{ 'active' : deletee==('qwertymy'+index) }">
                                        <svg width="19" height="15" viewBox="0 0 19 15" fill="none" xmlns="http://www.w3.org/2000/svg">
                                            <path fill-rule="evenodd" clip-rule="evenodd" d="M6.50038 0H17.2184C18.2006 0 18.9996 0.798956 18.9995 1.78119V12.4684C18.9995 13.4506 18.2006 14.2496 17.2183 14.2496H6.50038C5.96403 14.2496 5.46131 14.0119 5.12157 13.5967L0.133954 7.50051C-0.0446512 7.28195 -0.0446512 6.96765 0.133954 6.74909L5.12097 0.653443C5.46137 0.237745 5.96403 0 6.50038 0ZM17.2184 13.0621C17.5459 13.0621 17.8121 12.796 17.8121 12.4684L17.812 1.78113C17.812 1.45358 17.5459 1.18742 17.2183 1.18742H6.50038C6.32118 1.18742 6.15365 1.26687 6.03999 1.40545L1.36086 7.12477L6.04058 12.8447C6.15365 12.9827 6.32124 13.0621 6.50038 13.0621H17.2184ZM12.8164 4.15614C13.0483 3.9243 13.4241 3.9243 13.656 4.15614C13.8878 4.38798 13.8878 4.76386 13.656 4.9957L12.234 6.4177C11.8435 6.80823 11.8435 7.44139 12.234 7.83192L13.656 9.25392C13.8878 9.48576 13.8878 9.86164 13.656 10.0935C13.4241 10.3253 13.0483 10.3253 12.8164 10.0935L11.3944 8.67148C11.0039 8.28096 10.3707 8.28096 9.98021 8.67148L8.55821 10.0935C8.32637 10.3253 7.95048 10.3253 7.71864 10.0935C7.4868 9.86164 7.4868 9.48576 7.71864 9.25392L9.14064 7.83192C9.53117 7.44139 9.53117 6.80823 9.14064 6.4177L7.71864 4.9957C7.4868 4.76386 7.4868 4.38798 7.71864 4.15614C7.95048 3.9243 8.32637 3.9243 8.55821 4.15614L9.98021 5.57814C10.3707 5.96866 11.0039 5.96866 11.3944 5.57814L12.8164 4.15614Z"
                                            fill="white" />
                                        </svg>Удалить
                                    </button>
                                    </div>
                            </div>
                        </div>

                    </div>

                <div class="section-2 col-12">
                    <div class="row" >
                        <div class="mar-bot section-13 mt-3" v-for="(item,i) in orderone.details" :key="i">
                            <div class="eat-card-2  mt-2" v-for="(items,ii) in item.dishes" :key="ii">
                             <div>
                                <div class="first">
                                    <div class="eat-img">
                                        <img :src="items.dish[0].poster" alt="" >
                                    </div>
                                    <div class="dop-info">
                                         <div class="eat-name">{{items.dish[0].title}}</div>
                                        <div class="eat-sub">{{items.dish[0].description}}</div>
                                        <div class="eat-info"><span>С/п: {{items.dish[0].price}} uzs </span><span><img src="../assets/images/icons/clock.svg" alt=""> 15м</span>
                                        </div>
                                    </div>
                                </div>
                             </div>
                            </div>
                        </div>
                    </div>
                </div>
                </div>


            </div>

            <footer>
                <div class="my-footer">
                    <div class="navbar-container">
                        <div class="row"></div>
                    </div>
                </div>
            </footer>
        </div>
    </div>
</template>
<script>

    import {
         mapGetters
     } from 'vuex'
    import axios from 'axios'
    import { API } from '../store/config.js'

export default {
    data(){
        return{
            itemm:[],
            sum:0,
            mony:0,
            count:0,
            deletee:'',
            IsDishes:'',
            CountID:[],
            order:[],
            orderone:[],
            loading:false,
            OrderId:null

        }
    },
    mounted() {
        this.sum=this.mony*this.$store.state.foodslist.length
        for(var i=0;i<this.$store.state.foodslist.length;i++){
          this.order.push({
                "id":this.$store.state.foodslist[i].food.id,
                "amount":this.$store.state.foodslist[i].count
             }
            )
            this.sum=this.sum+this.$store.state.foodslist[i].food.price
        }



    },
    methods: {
        minus(foodslist,index,count)
        {
            console.log(count);
            if(foodslist[index].count>1){
            foodslist[index].count--
            this.order[index].amount--
            if(this.sum>0){
            this.sum=this.sum-foodslist[index].food.price
            }

            }
        },
        pilus(foodslist,index,count)
        { console.log(count);
            foodslist[index].count++,
            this.order[index].amount++
            this.sum=this.sum+foodslist[index].food.price
            console.log(this.order);
        },
        orderpost(){
            const token = localStorage.getItem('D-Token')
            if(this.$store.state.check.id==undefined){
             axios
            .post (API+'order/create',this.order,
            {
                    headers: {
                    Authorization: `Bearer ${token}`
                  }
                }

                )
                  .then(res=>{
                    console.log(res.data.data);
                    this.orderone=res.data.data,
                    this.$store.dispatch("check",this.orderone);
                    this.OrderId=res.data.data.id
                    this.$toasted.show("The order was accepted" , {
                    duration: 2000,
                })
                this.loading=false
                  })
        }
        else{
            axios
            .post (API+'order/'+this.$store.state.check.id,this.order,
            {
                    headers: {
                    Authorization: `Bearer ${token}`
                  }
                }

                )
                  .then(res=>{
                    this.orderone=res.data.data,
                    this.$store.dispatch("check",this.orderone);
                    this.OrderId=res.data.data.id;
                    this.$toasted.show("The order was accepted" , {
                    duration: 2000,
                })
                this.loading=false
                  })
        }
    },

    clear()
        {

        }

    },
    computed: {
             ...mapGetters(["data","foodslist","check"])
         },
}
</script>
<style>
    body{
        overflow-x: hidden;
    }
    .section-13{
        z-index: 1;
        opacity: 0.5;
        background-color: rgba(255, 255, 255, 0.063);
        backdrop-filter: blur(10px);
        width: 100%;
    }
    .img_child{
      height: 120px;
    }
</style>
